/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
  Button,
  Form,
  Row,
  Col,
  Popconfirm,
  Modal,
  Spin,
  Typography,
  Select,
  Input,
  Tag,
  Image,
} from "antd";
import {
  DeleteOutlined,
  EditOutlined,
  LoadingOutlined,
  PlusOutlined,
} from "@ant-design/icons";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
  useRef,
} from "react";
import Pagination from "../components/Pagination";
import { AntdMsg } from "../../utils/Antd";
import service from "../../services/homepageItems";
import serviceProduct from "../../services/product";
import offerService from "../../services/offer";
import categoryService from "../../services/category";
import bannerService from "../../services/banner";
import util from "../../utils/util";

export default function Product() {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState([]);
  const [qData, setQData] = useState({ page: 1, limit: 20 });
  const addNewModalRef = useRef();
  const [allProduct, setAllProduct] = useState([]);
  const [allCategories, setAllCategories] = useState([]);
  const [allOffers, setAllOffers] = useState([]);
  const [allBanners, setAllBanners] = useState([]);

  const columns = [
    {
      title: "#",
      width: 40,
      render: (v, row, i) => <b>{i + 1}</b>,
    },
    {
      title: "Product ",
      dataIndex: "productDetails",
      render: (v) => v.map((vv) => <Tag>{vv.name}</Tag>),
    },
    {
      title: "Categories ",
      dataIndex: "categoryDetails",
      render: (v) => v.map((vv) => <Tag>{vv.name}</Tag>),
    },
    {
      title: "Banners ",
      dataIndex: "bannerDetails",
      render: v => v.map(vv => <Image src={vv?.imgUrl} height={80} />)
    },
    {
      title: "Offer ",
      dataIndex: "offerDetails",
      render: (v) => v.map((vv) => <Tag>{vv.title}</Tag>),
    },
    {
      title: "Display Order",
      dataIndex: "displayOrder",
      width: 250,
    },
    {
      title: "Display Type",
      dataIndex: "displayType",
      width: 250,
    },
    {
      title: "Slug",
      dataIndex: "slug",
      width: 150,
    },
    {
      title: "Action",
      dataIndex: "_id",
      width: 70,
      render: (v, row) => {
        return (
          <>
            <Popconfirm
              title="Are you sure to delete this data?"
              onConfirm={() => {
                deleteData(row._id);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                size="small"
                danger
                icon={<DeleteOutlined />}
              />
              &nbsp;
            </Popconfirm>
            <Button
              type="primary"
              size="small"
              icon={<EditOutlined />}
              onClick={() => {
                addNewModalRef.current.openForm(row);
              }}
            />
          </>
        );
      },
    },
  ].filter((item) => !item.hidden);
  function list() {
    setLoading(true);
    service
      .list(qData)
      .then((res) => {
        setData(res.data?.map((v) => ({ ...v, key: v._id })));
        setQData({
          ...qData,
          limit: res.extra.limit,
          page: res.extra.page,
          total: res.extra.total,
        });
      })
      .catch((err) => {})
      .finally(() => {
        setLoading(false);
      });
  }
  useEffect(() => {
    list();
  }, []);
  const deleteData = (id) => {
    service
      .delete(id)
      .then((res) => {
        AntdMsg(res.message);
        list();
        setSelected([]);
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      });
  };
  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";
  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    title: () => (
      <Search
        {...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
      />
    ),
    showHeader: true,
    footer: () => <Pagination {...{ qData, setQData }} />,
    rowSelection: {
      onChange: (selectedRowKeys) => {
        setSelected(selectedRowKeys);
      },
    },
    tableLayout: undefined,
  };
  useEffect(() => {
    serviceProduct.listUser({ isAll: 1 }).then((res) => {
      setAllProduct(
        res.data.map((v) => ({ ...v, label: v.name, value: v._id }))
      );
    });
    offerService.list({ isAll: 1 }).then((res) => {
      setAllOffers(
        res.data.map((v) => ({ ...v, label: v.title, value: v._id }))
      );
    });
    categoryService.list({ isAll: 1 }).then((res) => {
      setAllCategories(
        res.data.map((v) => ({ ...v, label: v.name, value: v._id }))
      );
    });
    bannerService.bannerList({ isAll: 1 }).then((res) => {
      setAllBanners(
        res.data.map((v) => ({ ...v, label: v.name, value: v._id }))
      );
    });
  }, [qData.page, qData.limit]);
  return (
    <>
      <Typography.Title level={5} style={{ marginTop: 0 }}>
        Product List
      </Typography.Title>
      <Table
        {...tableProps}
        pagination={{ position: ["none"], pageSize: qData.limit }}
        columns={tableColumns}
        dataSource={data.length ? data : []}
        scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
      />
      <AddForm
        ref={addNewModalRef}
        {...{ list, allProduct, allBanners, allCategories, allOffers }}
      />
    </>
  );
}

function Search({
  addNewModalRef,
  selected,
  deleteData,
  qData,
  setQData,
  list,
}) {
  return (
    <Form onSubmitCapture={list} className="search-form">
      <Row gutter={[12, 2]}>
        <Col span={4}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Input
              placeholder="Search by name or code"
              value={qData.key}
              onChange={(e) => setQData({ ...qData, key: e.target.value })}
              allowClear
            />
          </Form.Item>
        </Col>
        <Col span={3}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Button type="primary" htmlType="submit">
              Search
            </Button>
          </Form.Item>
        </Col>
        <Col span={5}>
          <Form.Item style={{ marginBottom: 0, float: "right" }}>
            <Button
              type="primary"
              icon={<PlusOutlined />}
              onClick={() => {
                addNewModalRef.current.openForm();
              }}
            >
              Add New
            </Button>
          </Form.Item>
          {selected.length ? (
            <Popconfirm
              title="Are you sure to delete these selected data ?"
              onConfirm={() => {
                deleteData(selected);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                danger
                style={{ float: "right" }}
                icon={<DeleteOutlined />}
              >
                Delete Selected
              </Button>
            </Popconfirm>
          ) : null}
        </Col>
      </Row>
    </Form>
  );
}

const AddForm = forwardRef((props, ref) => {
  const { list, allProduct, allBanners, allCategories, allOffers } = props;
  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });
  const [ajxRequesting, setAjxRequesting] = useState(false);

  const handleChange = (v, k) => {
    let varDt = data;
    let keys = k.split(".");
    for (let i = 0; i < keys.length; i++) {
      if (i + 1 === keys.length) {
        varDt[keys[i]] = v;
      } else {
        if (typeof varDt[keys[i]] === "undefined") {
          if (parseInt(keys[i + 1]) * 1 >= 0) {
            varDt[keys[i]] = [];
          } else {
            varDt[keys[i]] = {};
          }
        }
        varDt = varDt[keys[i]];
      }
    }
    setData({ ...data });
  };
  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(dt ? { ...dt } : {});
    },
  }));

  const save = () => {
    setAjxRequesting(true);
    service
      .save(data)
      .then((res) => {
        AntdMsg(res.message);
        handleChange({ ...res.data });
        list();
        setOpen(false);
      })
      .catch((err) => {
        if (typeof err.message === "object") {
          let dt = err.message[Object.keys(err.message)[0]];
          AntdMsg(dt, "error");
        } else {
          AntdMsg(err.message, "error");
        }
      })
      .finally(() => {
        setAjxRequesting(false);
      });
  };

  const getDetails = () => {
    setAjxRequesting(true);
    service
      .details(data._id)
      .then((res) => {
        setData(res.data || {});
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      })
      .finally(() => {
        setAjxRequesting(false);
      });
  };
  useEffect(() => {
    if (data._id) {
      getDetails();
    }
  }, [data._id]);
  useEffect(() => {
    if (!data._id) {
      handleChange(util.removeSpecialChars(data.slug || ""), "slug");
    }
  }, [data.slug]);
  useEffect(() => {
    if (!open) {
      setData({});
    }
  }, [open]);

  return (
    <>
      <Modal
        title={(!data._id ? "Add New" : "Edit") + " Product"}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onOk={save}
        okButtonProps={{ disabled: ajxRequesting }}
        onCancel={() => {
          setOpen(false);
        }}
        destroyOnClose
        maskClosable={false}
        width={1000}
        className="app-modal-body"
        footer={[
          <Button
            key="cancel"
            onClick={() => {
              setOpen(false);
            }}
          >
            Cancel
          </Button>,
          <Button key="save" type="primary" onClick={save}>
            Save
          </Button>,
        ]}
      >
        <Spin
          spinning={ajxRequesting}
          indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
        >
          <Form layout="vertical">
            <Row gutter={[12, 2]}>
              <Col span={12}>
                <Form.Item label="Type" required>
                  <Select
                    value={data.type}
                    onChange={(e) => {
                      handleChange(e, "type");
                    }}
                  >
                    <Select.Option value="category">Category</Select.Option>
                    <Select.Option value="banner">Banner</Select.Option>
                    <Select.Option value="offer">Offer</Select.Option>
                    <Select.Option value="products">Products</Select.Option>
                  </Select>
                </Form.Item>
              </Col>

              {data.type === "products" && (
                <Col span={24}>
                  <Form.Item label="Choose Product" required>
                    <Select
                      mode="multiple"
                      placeholder="Choose Products"
                      onChange={(v) => {
                        handleChange(v, "productIds");
                      }}
                      optionLabelProp="label"
                      allowClear={true}
                      value={data.productIds}
                      options={allProduct.filter((v) => {
                        return true;
                      })}
                    />
                  </Form.Item>
                </Col>
              )}
              {data.type === "banner" && (
                <Col span={24}>
                  <Form.Item label="Choose Banners" required>
                    <Select
                      mode="multiple"
                      placeholder="Choose Banners"
                      onChange={(v) => {
                        handleChange(v, "bannerIds");
                      }}
                      optionLabelProp="label"
                      allowClear={true}
                      value={data.bannerIds}
                      options={allBanners.filter((v) => {
                        return true;
                      })}
                    />
                  </Form.Item>
                </Col>
              )}
              {data.type === "category" && (
                <Col span={24}>
                  <Form.Item label="Choose Categories" required>
                    <Select
                      mode="multiple"
                      placeholder="Choose Categories"
                      onChange={(v) => {
                        handleChange(v, "categoryIds");
                      }}
                      optionLabelProp="label"
                      allowClear={true}
                      value={data.categoryIds}
                      options={allCategories.filter((v) => {
                        return true;
                      })}
                    />
                  </Form.Item>
                </Col>
              )}
              {data.type === "offer" && (
                <Col span={24}>
                  <Form.Item label="Choose Offers" required>
                    <Select
                      mode="multiple"
                      placeholder="Choose Offers"
                      onChange={(v) => {
                        handleChange(v, "offerIds");
                      }}
                      optionLabelProp="label"
                      allowClear={true}
                      value={data.offerIds}
                      options={allOffers.filter((v) => {
                        return true;
                      })}
                    />
                  </Form.Item>
                </Col>
              )}
              <Col span={12}>
                <Form.Item label="Title">
                  <Input
                    placeholder="title"
                    value={data.title}
                    onChange={(e) => {
                      handleChange(e.target.value, "title");
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Slug" required>
                  <Input
                    placeholder="slug"
                    value={data.slug}
                    onChange={(e) => {
                      handleChange(
                        util.removeSpecialChars(e.target.value),
                        "slug"
                      );
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Display Order" required>
                  <Input
                    placeholder="1"
                    type="number"
                    value={data.displayOrder}
                    onChange={(e) => {
                      handleChange(e.target.value, "displayOrder");
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Display Type" required>
                  <Select
                    value={data.displayType}
                    onChange={(e) => {
                      handleChange(e, "displayType");
                    }}
                  >
                    <Select.Option value="grid">Grid</Select.Option>
                    <Select.Option value="scroll">Scroll</Select.Option>
                  </Select>
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </Spin>
      </Modal>
    </>
  );
});
